import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const state={
    menu:{},
    menus:[{
        id:'1',
        name:'HttpTool',
        code:'http',
        items:[{
            id:'101',
            name:'http模拟',
            code:'http1',
            icon:'',
            url:'/'
        },{
            id:'102',
            name:'http模拟2',
            code:'http2',
            icon:'',
            url:'/'
        }]
    },{
        id:'2',
        name:'jsonTool',
        code:'json',
        items:[{
            id:'201',
            name:'json格式化',
            code:'json1',
            icon:'',
            url:'/'
        },{
            id:'202',
            name:'json检测',
            code:'json2',
            icon:'',
            url:'/'
        }]
    }]

}

const mutations={
    getMenus(state,pCode,code){
        for(var i=0;i<state.menus.length;i++){
            var childMenus=state.menus[i];
            if(childMenus.code==pCode){
                for(var j=0;j<childMenus.items.length;j++){
                    var item=childMenus.items[j];
                    if(item.code==code){
                        state.menu=item;
                        break;
                    }
                }
            }
        }
    }
}

export default new Vuex.Store({
    state,
    mutations
})


